.create-feed-container {
    padding: 1rem;

    .upload-section {
        text-align: center;

        padding: 1rem;
        border-radius: 29px;

        border: 2px dotted #ababab;

    }

    .primaryButton {
        width: 100%;
    }

    .upload-selection-box {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        img{
            max-width: 100px;
        }
    }
         .create-post-navigation-header {
             display: flex;
             justify-content: space-between;
             align-items: center;
             padding: 20px;
             font-size: 18px;
         }
    
         .create-feed-box {
             padding: 20px;
             background-color: #fff;
             border-radius: 8px;
            //  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    
             .upload-section {
                 display: flex;
                 justify-content: center;
                 align-items: center;
                 margin-bottom: 20px;
                 min-height: 200px;
    
                
    
                 input[type="file"] {
                     display: none;
                 }
             }
        .title-input input{
            border: none;
                border-bottom: 1px solid #6c6c6c;
                width: 100%;
                padding-bottom: 12px;
        }
                .title-input input:focus {
                   border: none;
                }
             /* Carousel Section */
             .file-carousel-container {
                 display: flex;
                 flex-direction: column;
                 align-items: center;
                //  margin-top: 20px;
                position: relative;
                 .file-preview-card {
                     width: 100%;
                     max-width: 400px;
                     height: 300px;
                     overflow: hidden;
                     margin-bottom: 20px;
                     position: relative;
                     border-radius: 25px;
    
                     .preview-image {
                         width: 100%;
                         height: 100%;
                         object-fit: cover;
                     }
    
                     .preview-video {
                         width: 100%;
                         height: 100%;
                         object-fit: cover;
                     }
                 }
    
                 .carousel-dots {
                     display: flex;
                     justify-content: center;
                     gap: 8px;
                     margin-bottom: 10px;
    
                     .dot {
                         width: 12px;
                         height: 12px;
                         background-color: #ccc;
                         border-radius: 50%;
                         cursor: pointer;
                         transition: background-color 0.3s ease;
                     }
    
                     .dot.active {
                         background-color: #007bff;
                     }
                 }
    
                 .carousel-info {
                    font-size: 14px;
                        text-align: center;
                        color: #555;
                        position: absolute;
                        right: 10px;
                        top: 10px;
                        font-weight: 500;
                        color: #fff;
                 }
             }
    
             .buttons-section {
                 margin-top: 20px;
                 display: flex;
                 justify-content: center;
             }
         }
.primaryButton{
    margin-top: 6.5rem;
}
.create-post-navigation-header{
    font-weight: 500;
}
.form-input:focus {
    outline: none;
    border-bottom: 2px solid #000  !important;
}
}